
<template>
	<div id="app">
		<div class="page">
			<div class="panel" ref="panel">
				
			</div>
		</div>
	</div>
</template>

<script>
	import * as d3 from 'd3';
    export default {
		data() {
			  return {
				  query: {"fastaInput":"","forSelect":"knock-out",
				  "geneInput":"chr11:37719830-37720130","isIsoform":false,
				  "opts":["-J","-BED","-GenBank","-G","danRer11",
				  "-filterGCmin","10","-filterGCmax","90","-t","CODING",
				  "-n","N","-R","4","-3",
				  "PRODUCT_SIZE_MIN=150,PRODUCT_SIZE_MAX=290,PRIMER_MIN_SIZE=18,PRIMER_MAX_SIZE=25,PRIMER_OPT_SIZE=22,PRIMER_MIN_TM=57,PRIMER_MAX_TM=63,PRIMER_OPT_TM=60",
				  "-P","-A","290","-DF","300","-a","20","-T","1","-g","20",
				  "-scoringMethod","DOENCH_2016","-f","NN","-v","3",
				  "-repairPredictions","mESC","-M","NGG","-BB","AGGCTAGTCCGT"]},
				  cutcoords: [[1, 37719979, -67.04956064879752, 23, "+", 0], 
				  [2, 37719982, -62.20696157720151, 23, "-", 1], 
				  [3, 37720099, -50.071126090429956, 23, "+", 1], 
				  [4, 37720015, 334.66899656274416, 23, "-", 0], 
				  [5, 37719808, 348.9519765279375, 23, "-", 0], 
				  [6, 37719993, 1342.3926680589825, 23, "-", 2], 
				  [7, 37719944, 1520.7203769439705, 23, "-", 0], 
				  [8, 37719947, 3137.4343859929695, 23, "-", 1], 
				  [9, 37720091, 5538.534025064337, 23, "+", 0], 
				  [10, 37720116, 15751.695578893281, 23, "-", 0], 
				  [11, 37719909, 45361.250828484495, 23, "+", 0]],
				  genemin: 37719830, 
				  genemax: 37720130,
				  w: '',
				  xScale: '',
				  xScale2: '',
				  margin: {
					  top: 20,
					  bottom: 20,
					  left: 5,
					  right: 20
				  },
				  w: '',
				  tierPx: 17,
				  genePx: 30,
				  tiers: '',
				  h: '',
				  iso_h: '',
				  xAxis: '',
				  xAxisGrid: '',
				  maxScaleFactor: '',
				  sExtM: '',
				  zoom: '',
				  svg: '',
				  gX: '',
				  gXgrid: '',
				  colorscale: '',
			  }
			  
		},
		computed: {
		},
		filters: {},
		created() {},
		mounted() {
			this.w = this.$refs.panel.offsetWidth - this.margin.left - this.margin.right;	
			this.tiers = d3.max(this.cutcoords, function(d) {return d[d.length - 1];});
			this.h = (Math.max(this.tiers, 5) * this.tierPx) + 10 + 30 - this.margin.top - this.margin.bottom;
			this.iso_h = (Math.max(this.tiers, 5) * this.tierPx) + 10 - this.margin.top;
			this.xScale = d3.scaleLinear().domain([this.genemin, this.genemax]).range([0, this.w]);
			this.xScale2 = d3.scaleLinear().domain([this.genemin, this.genemax]).range([0, this.w]);
			this.xAxis = d3.axisBottom(this.xScale).ticks(10);
			this.xAxisGrid = d3.axisBottom(this.xScale).ticks(10).tickSize(-this.h, 0, 0).tickFormat("");
			this.maxScaleFactor = this.w / Math.abs(this.genemin - this.genemax);
			this.sExtM = this.w / Math.abs(this.xScale(100) - this.xScale(1));
			this.zoom = d3.zoom().scaleExtent([1, this.sExtM]).on("zoom", this.zoomed);
			
			
			this.svg = this.createPicZone();
			this.gX = this.drawNumLabelBelow();
			this.gXgrid = this.createGrid();
			
			var clip = this.createClip();
			
			//create chartBody
			this.chartBody = this.svg.append("g").attr("clip-path", "url(#clip)");
			this.createMarker();
			this.colorscale = d3.scaleThreshold().domain([30,70]).range(["green","orange","red"]);
			this.drawMainBarchart();
		},
        methods: {
			zoomed() {
				console.log("lalala");
				if (d3.event.sourceEvent && d3.event.sourceEvent.type === "brush") return; // ignore zoom-by-brush
				this.xScale.domain(d3.event.transform.rescaleX(this.xScale2).domain());
				this.gX.call(this.xAxis);
				this.gXgrid.call(this.xAxisGrid);
				this.svg.selectAll(".cutsite")
					.attr("x1", (d) => this.xScale(d[1] - 1))
					.attr("x2", (d) => this.xScale(d[1] + d[3] - 1));
				//this.textRank.attr("x", (d) => xScale(d[1] + (d[3] / 2)));
				//exons.attr("x1", (d) => xScale(d[1])).attr("x2", (d) => xScale(d[2]));
				//intron1.attr("x1", (d) => xScale(d[2])).attr("x2", (d) => xScale(d[2] + d[3] / 2));
				//intron2.attr("x1", (d) => xScale(d[2] + d[3])).attr("x2", (d) => xScale(d[2] + d[3] / 2));
				//atg.attr("x1", (d) => xScale(d.loc - 0.5)).attr("x2", (d) => xScale(d.loc + 0.5));
			},
			createPicZone() {
				var svg = d3.select(".panel")
					.append("svg:svg")
					.attr("width", this.w + this.margin.left + this.margin.right)
					.attr("height", this.h + this.margin.top + this.margin.bottom)
					.call(this.zoom)
					.append("svg:g")
					.attr("transform", "translate(" + this.margin.left + "," + this.margin.top + ")");
				console.log("hhh");
				return svg;
			},
			drawNumLabelBelow() {
				var gX = this.svg.append("svg:g")
					.attr("class", "x axis")
					.attr("transform", "translate(0, " + this.h + ")")
					.call(this.xAxis);
				return gX;
			},
			createGrid() {
				var gXgrid = this.svg.append("g")
					.attr("class", "x grid")
					.attr("transform", "translate(0," + this.h + ")")
					.call(this.xAxisGrid);
				return gXgrid;
			},
			createClip() {
				var clip = this.svg.append("svg:clipPath")
					.attr("id", "clip")
					.append("svg:rect")
					.attr("x", 0)
					.attr("y", 0)
					.attr("width", this.w)
					.attr("height", this.h);
				return clip;
			},
			createMarker() {
				this.svg.append("defs")
					.append("marker")
					.data(this.cutcoords)
					.attr("id", "forward_arrow")
					.attr("refX", 0)
					.attr("refY", 0.5)
					.attr("markerWidth", 6)
					.attr("markerHeight", 1)
					.attr("orient", "auto")
					.append("path")
					.attr("d", "M 0,0 V 1 L0.5,0.5 Z");
				this.svg.append("defs")
					.append("marker")
					.data(this.cutcoords)
					.attr("id", "reverse_arrow")
					.attr("refX", 0.5)
					.attr("refY", 0.5)
					.attr("markerWidth", 6)
					.attr("markerHeight", 2)
					.attr("orient", "auto")
					.append("path")
					.attr("d", "M 0.5,0 V 1 L0,0.5 Z");
			},
			drawMainBarchart() {
				this.chartBody.selectAll("cutsite")
					.data(this.cutcoords)
					.enter().append("line")
					.attr("marker-start", (d) => {
						let arr = (!this.query.isIsoform & d[4] == '-');
						return (arr) ? 'url(#reverse_arrow)' : 'none';
					})
					.attr("marker-end", (d) => {
						let arr = (!this.query.isIsoform & d[4] == '+');
						return arr ? 'url(#forward_arrow)' : 'none';
						})
					.attr("class", "cutsite")
					.attr("id", (d) => d[0])
					.attr("x1", (d) => this.xScale(d[1] - 1))
					.attr("x2", (d) => this.xScale(d[1] + d[3] - 1))
					.attr("y1", (d) => this.iso_h - 25 - d[d.length - 1] * 12)
					.attr("y2", (d) => this.iso_h - 25 - d[d.length - 1] * 12)
					.attr("stroke-width", 10)
					.attr("height", 10)
					.attr("stroke", (d) => this.colorscale(d[2]));
			},
  
			
        
        }
        
	
    };
</script>

<style>
#home {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
  width: 100%;
}

.page {
	margin-top: 0px;
	display: flex;
	display: -webkit-flex;
	flex-direction: column;
	width: 100%;	
}

.panel {
	width: 100%;
	border-style: solid;
	border-color: #000000;
	border-width: 1px;
	padding-top: 0;
}

.grid path {
    stroke-width: 0;
}

.axis path,
.axis line {
    fill: none;
    stroke: #000;
    shape-rendering: crispEdges;
}

.x.axis path {
    display: none;
}

</style>
